<template>
  <div class="reading-panel">
    <div @click="showPanel('recorderPanelVisible')">朗读活动页</div>
    <template v-if="chapter">
        <template v-if="chapter.quesType == QUES_TYPES.TXT">
            <div class="text">{{chapter.text}}</div>
        </template>
        <template v-if="chapter.ansType == ANS_TYPES.AUD" class="audio-panel">
            <recorder-panel 
            v-show="visible.recorderPanelVisible"
            @gotoReplay="gotoReplay">
            </recorder-panel>
            <player-panel 
                v-show="visible.playerPanelVisible" 
                :enableBack="enable.recorderPanelEnable"
                @openCatelog="openCatelog"
                @back="backtoRecorder">
            </player-panel>
        </template>
        <catelog-panel 
        :catelog="catelog" 
        @close="closeCatelog" 
        @selectItem="selectItem" 
        v-show="visible.catelogVisible">
        </catelog-panel>
    </template>
  </div>
</template>

<script>
import PlayerButton  from '../utils/PlayerButton';
import ProgressBar from '../utils/ProgressBar';
import PlayerPanel from './answer/PlayerPanel';
import RecorderPanel from './answer/RecorderPanel';
import CatelogPanel from './CatelogPanel';
import { Button } from 'mint-ui';

export default {
    name: "Reading",
    data(){
        return {
            ANS_TYPES:{
                "AUD": 1,
            },
            QUES_TYPES:{
                "TXT": 1
            },
            catelog:null,
            chapter: null,
            visible:{
                catelogVisible:false,
                recorderPanelVisible:false,
                playerPanelVisible:false
            },
            enable:{
                recorderPanelEnable:true,
                playerPanelEnable:true
            },
            own:1,
            complete:0,
            overTime:0
        }
    },
    beforeRouteUpdate (to, from, next) {
       // alert(123);
        this.$audio.pause();
        next();
    },
    beforeRouteLeave (to, from, next){
        this.$audio.pause();
        next();
    },
    created(){
        let id = this.$route.params.id;
        this.chapter = this.getChapter();
        this.catelog = this.getCatelog();

        //(拥有/非拥有)决定面板是否可以操作
        //活动是否完成
        //活动时间决定拥有者是否可以录音或者再录音
        
        let current = sprintf("%s%s%s", this.own,this.complete, this.overTime);

        switch(current){
            case "100":
                //拥有,未完成,活动内 => 录音界面
                this.enable.recorderPanelEnable = true;
                this.showPanel('recorderPanelVisible');
            break;
            case "110":
                //拥有,"完成,活动内" => 播放界面,可重录
                this.enable.recorderPanelEnable = true;
                this.showPanel('playerPanelVisible');
            break;
            default:
                //播放界面不可重录
                this.enable.recorderPanelEnable = false;
                this.showPanel('playerPanelVisible');
            break;
        }        
    },
    methods:{
        getChapter(){
            //TODO获取本章节数据
            return {
                id: 1,
                text: "白雪公主在国王和王后的宠爱之下，逐渐长大了，终于成了一个人见人爱的美少女。白雪公主非常善良、有爱心、她经常和动物一起玩耍。森林的动物，像小鹿、小兔子、松鼠、小鸟都喜欢白雪公主，因为白雪公主会给它们吃食物，还会讲故事给它们听。个性善良犹如天使般的白雪公主，过着幸福快乐的生活。　　可是，好景不长，白雪公主的母亲生病去世了。　　国王为了白雪公主就迎娶了一位新王后，可是，这位新王后却是个精通法术的女巫。她虽然很美丽，但是个性很骄傲、暴躁。尤其她最恨别人比她美丽",
                align: "left",
                aud: "",
                maxTime: "",
                owner: "小李",
                quesType: 1,
                ansType: 1,
                state: 1
            }
        },
        getCatelog(){
            return [
                {
                    id: 1,
                    title: "第一节",
                    owner: "小菜",
                    time: "10:00",
                    img: ""
                },
                {
                    id: 2,
                    title: "第二节",
                    owner: "小吧",
                    time: "10:00",
                    img: ""
                },
                {
                    id: 3,
                    title: "第三节",
                    owner: "小酒",
                    time: "10:00",
                    img: ""
                }
            ]
        },
        gotoReplay(){
            this.showPanel('playerPanelVisible');
        },
        openCatelog(){
            this.showPanel('catelogVisible');
        },
        closeCatelog(){
            this.showPanel('playerPanelVisible');
        },
        backtoRecorder(){
            this.showPanel('recorderPanelVisible');
        },
        //显示指定面板，隐藏其他面板
        showPanel(name){
            for(var key in this.visible){
                this.visible[key] = false;
            }
            this.visible[name] = true;
        },
        selectItem(item){
            this.$router.push({name: "Reading", params:{id:item.id}});
        }
    },
    computed:{

    },
    components:{
        PlayerButton,
        ProgressBar,
        PlayerPanel,
        RecorderPanel,
        CatelogPanel
    }
}
</script>

<style lang="scss" scoped>
@import "../../assets/css/global.scss";
.reading-panel{
    position: relative;
    .catelog-panel{
        background: #fff;
        position: fixed;
        bottom:0;
        border:1px solid #ccc;
        width:100%;
        .title{
            border-bottom: 1px solid #ccc;
            position: relative;
            .close{
                position: absolute;
                right:0
            }
        }
        li{
            padding: vw(10);
            border-bottom: 1px solid #ccc;
        }
        li:last-child{
            border-bottom:none
        }
    }
}
</style>



