<template>
<div class="progress-bar">
  <div>{{current}}</div>
  <div :style="{width:progressWidth + 'vw'}">
    <mt-progress :value="trackWidth" @click.native="progressClick"></mt-progress>
  </div>
  <div>{{total}}</div>
</div>
</template>

<script>
import {Progress} from 'mint-ui'
export default {
  name: "ProgressBar",
  created(){
    
  },
  data(){
    return {
      EVENTS:{
        PROGRESS_CLICK:'progressClick'
      },
      width: 300,
    }
  },
  props:['total', 'current'],
  methods:{
    progressClick(event){
      let width = this.progressWidth / 100 * event.view.innerWidth;
      let offsetX = event.offsetX;

      this.val = offsetX/width * this.total;
      
      this.$emit(this.EVENTS.PROGRESS_CLICK, this.val.toFixed());
    }
  },
  computed:{
    progressWidth(){
      return (this.width/750)*100;
    },
    trackWidth(){
      return this.total?this.current / this.total * 100:0;
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../../assets/css/global.scss";
  .progress-bar>*{
    width: vw(100);
    display: inline-block;
  }
</style>





