const AudioIF = {
    install:function(Vue, options) {
      Vue.prototype.$audio = function () {
          let player = require('howler');
          let Howler = player['Howler'];
          let Howl = player['Howl'];
          let audio = null
          let soundId = null;
          let seek = 0;
          let defaultOps = {
            volumn: .5,
            html5: false
          }
          let audOps = null;

          //创建音频
          function create(ops){
            audOps = Object.assign({}, defaultOps, ops, 
            {
              onplay: funcMerge(ops['onplay'], function(){
                if(ops["onplaying"] && audio.playing())requestAnimationFrame(timeDuration.bind(null, ops["onplaying"]));    
              })
            });

            let audio = new Howl(audOps);

            return audio;
          }

          //播放中时间
          function timeDuration(callback){  
            let seek = audio.seek() || 0
            callback.call(null, seek.toFixed());

            if(audio.playing())requestAnimationFrame(timeDuration.bind(null, callback));
          }

          function funcMerge(){
            let argus = arguments;
          
            return function(){
              for(let key in argus){
                if(argus[key])argus[key].call(null);
              }
            }
          }

          return{
              play(){
                //停止上一次录音
                if(audio)audio.stop();

                //创建并开始播放
                audio = create.apply(null, arguments);
                soundId = audio.play();
                
                return soundId;
              },
              pause(){
                if(audio){
                  audio.pause(soundId);
                  //保存暂停点
                  seek = audio.seek(soundId);
                }
              },
              stop(){
                if(audio){
                  audio.stop();
                }
              },
              resume(){
                if(audio){
                  audio.play(soundId);
                  //恢复到播放点
                  audio.seek(seek, soundId);
                }
              },
              //获取音频时长
              duration(){
                if(audio)return audio.duration(soundId);
              },
              seek(number){
                if(audio)audio.seek(number);
              }
          }
      }();
    }
  }
  
  export default AudioIF;
  
  
