<template>
  <div>
      <div class="desc-box">{{text}}</div>
      <div class=""><b>开启共读之旅</b></div>
      <ul>
        <li v-for="(chapter, index) in chapters" @click="enterChapter(chapter)">
            <template v-if="chapter.state == STATES.UNLOCK">
                <template v-if="chapter.open == OPEN_STATES.OPEN">
                    <div class="unlock open">
                        <p>等待领取</p>
                        <p>第{{index + 1}}节</p>
                    </div>
                </template>
                <template v-if="chapter.open == OPEN_STATES.UNOPEN">
                    <div class="unlock unopen">
                        <p>未开启</p>
                        <p>第{{index + 1}}节</p>
                    </div>
                </template>
            </template>
            <template v-else-if="chapter.state == STATES.LOCK">
                <div class="lock">
                    <p>已领取</p>
                    <p>第{{index + 1}}节</p>
                    <p>{{chapter.owner}}</p>
                </div>
            </template>
            <template v-else-if="chapter.state == STATES.DONE">
                <div class="done">
                    <p>已完成</p>
                    <p>第{{index + 1}}节</p>
                    <p>{{chapter.owner}}</p>
                    <p v-if="chapter.aud" @click="playAudio(chapter)">播放</p>
                </div>
            </template>
        </li>
    </ul>
  </div>
</template>

<script>
import { Toast,MessageBox } from 'mint-ui';

export default {
    name:"Selection",
    data(){
        return {
            chapters: [],
            text: "",
            STATES: {
                "UNLOCK": 1,
                "LOCK": 2,
                "DONE": 3,
            },
            OPEN_STATES:{
                "UNOPEN": 0,
                "OPEN": 1
        }
    }
  },
  created(){
      console.log(this.$router);
      this.requestChapter();
      //TODO 获取文本切片数据 + 切片状态(切面领取人和未完成和完成)
  },
  methods:{
      //请求切片
      requestChapter(id){
          //requestChapter
          this.text = "白雪公主的童话作者是";
          this.chapters = [
            {
                id: 3,
                st: "白雪公主短文四",
                state: 3,
                aud: "test.mp3",
                owner: "龙哥",
                ownerId: "",
                open: 1
            },
            {
                id: 2,
                st: "白雪公主短文二",
                state: 2,
                owner: "龙哥",
                ownerId:"1",
                open: 1
            },
            {
                id: 1,
                st: "白雪公主短文一",
                state: 1,
                open: 1
            },
            {
                id: 3,
                st: "白雪公主短文四",
                state: 1,
                aud: "",
                owner: "龙哥",
                ownerId: "",
                open: 0
            }
          ]
      },
      //进入章节
      enterChapter(chapter){  
        
        let state = chapter.state;
        let open = chapter.open;

        //enterChapter
        if(open){
            let STATES = this.STATES;
            switch(state){
                case STATES.UNLOCK:
                    MessageBox.confirm("确定领取？", "领取章节").then(action => {
                        this.requestUnlockChapter(res => {
                            //TODO 完成领取
                        });
                    });
                break;
                case STATES.LOCK:
                    //TODO 提示已经上锁
                    MessageBox.alert(sprintf('章节已经被%s同学领取<br>10分钟后再来看看吧', chapter.owner));
                break;
                case STATES.DONE:
                break;
            }
        }else{
            //TODO 提示选择之前章节
            Toast({
                message: '章节还没打开，请先完成之前章节',
                position: 'bottom',
                duration: 1000
            });
        }
      },
      requestUnlockChapter(callback){
          //TODO 请求Chapter
      },
      //返回上一级
      back(){   
        //TODO 返回
      },
      playAudio(chapter){
        if(chapter.aud){
          let aud = chapter.aud;
          //TODO 播放音频
          Toast({
                message: '播放音频',
                position: 'bottom',
                duration: 1000
          });

        }
      }
  }
}

</script>

<style lang="scss" scoped>
@import "../../assets/css/global.scss";
.desc-box{
    min-height: vw(200);
    border:1px solid #000;
    margin: vw(40);
    text-align: left;
}

ul {
    li{
        display: inline-block;
        width: vw(300);
        height: vw(300);
        text-align: center;
        vertical-align: middle;
        margin: vw(30) vw(30);

        p{
            display: block;
        }

        >div{
            width: 100%;
            height:100%;
            border-radius: inherit;
            position: relative;
            display: inline-block;
            border-radius: vw(90);
        }
        .done{
            color:#fff;
            background-color:#333333;
            box-shadow: 2px 3px 10px rgba(204, 204, 204, 0.349019607843137);
            border-radius: vw(150)
        }

        .unlock.open{
            color: #333333;
            background-color:#fff;
            box-shadow: 2px 3px 10px rgba(204, 204, 204, 0.349019607843137);
            
        }

        .unlock.unopen{
            color: #333333;
            background-color: rgba(204, 204, 204, 1);
            box-shadow: 2px 3px 10px rgba(204, 204, 204, 0.349019607843137);
        }


        .lock{
            color:#fff;
            background-color:rgba(102, 102, 102, 1);
            box-shadow: 2px 3px 10px rgba(204, 204, 204, 0.349019607843137);
        }
    }
}
</style>





