<template>
  <ul>
      <li v-for="(activity, key) in activities" @click="selectActivity(activity)">
          <span>{{ activity.name }}</span>
          <span>{{ activity.startTime}} - {{ activity.endTime}}</span>
      </li>
  </ul>
</template>

<script>
export default {
    name:"Activity",
    data(){
        return {
