                id:1,
                name: "阅读活动二",
                startTime: "13:00",
                endTime: "16:00"
            },
            {
                id:2,
                name: "阅读活动三",
                startTime: "13:00",
                endTime: "16:00"
            }
        ]
    },
    methods:{
        selectActivity:() => {
            this.$router.push({name: "Selection", params:{id:item.id}});
        }
    }
}
</script>

