<template>
    <div class="player-tab">
        <div class="catelog-button" @click="openCatelog">目录</div>
        <progress-bar class="progress-bar" @progressClick="progressClick" :total="total" :current="duration"></progress-bar>
        <player-button class="player-button" @durationLoad="durationLoad" @timeChange="timeChange" ></player-button>
        <div v-if="enableBack" @click="back">返回</div>
    </div>
</template>

<script>
import PlayerButton  from '../../utils/PlayerButton';
import ProgressBar from '../../utils/ProgressBar';
export default {
    name:'PlayerPanel',
    components:{
        PlayerButton,
        ProgressBar
    },
    props:['enableBack'],
    data(){
        return {
            EVENTS:{
                OPEN_CATELOG: "openCatelog",
                BACK: "back",
            },
            audio: null,
            total: 0,
            duration: 0
        }
    },
    methods:{
        openCatelog(){
            this.$emit(this.EVENTS.OPEN_CATELOG);
        },
        back(){
            this.$emit(this.EVENTS.BACK);
        },
        progressClick(data){
            this.$audio.seek(data);
        },
        durationLoad(time){
            this.total = time;
        },
        timeChange(time){
            this.duration = time;
        }
    }
}
</script>

<style lang="scss" scoped>
@import "../../../assets/css/global.scss";
.player-tab{
    border-top:1px solid #ccc;
    position: fixed;
    width: 100%;
    bottom: 0;
    left:0;
    min-height: vw(200);
    display: flex;
    flex-direction: column;
    align-items: center;

    >div{
        flex-grow: 1;
    }
} 
 
</style>





