<template>
    <div class="recorder-tab">
        <div class="goto-replay-button" v-show="replayVisiable" @click="gotoReplay">试听</div>
        <record-button class="record-button" @changeEvent="stateChange"></record-button>
        <div class="upload-button" v-show="uploadVisiable" @click="upload">上传</div>
    </div>
</template>

<script>
import { Toast,MessageBox } from 'mint-ui';
import RecordButton from '../../utils/RecorderButton';
export default {
  name: 'RecorderPanel',
  data(){
      return {
          EVENTS:{
              GOTO_REPLAY:"gotoReplay",
              UPLOAD:"upload"
          },
          replayVisiable:false,
          uploadVisiable:false
      }
  },
  methods:{
      gotoReplay(){
        this.$emit(this.EVENTS.GOTO_REPLAY);
      },
      upload(){
        MessageBox.confirm("确认上传音频？", "确认上传").then(action => {
          Toast({
            message: '上传成功',
            position: 'bottom',
            duration: 1000
          });
        });
        this.$emit(this.EVENTS.UPLOAD);
      },
      recordClick(){
          
      },
      stateChange(val){
        this.replayVisiable = true,
        this.uploadVisiable = true
      }
  },
  components:{
      RecordButton
  }
}
</script>

<style lang="scss" scoped>
@import "../../../assets/css/global.scss";
.recorder-tab{
    border-top:1px solid #ccc;
    position: fixed;
    width: 100%;
    bottom: 0;
    left:0;
    min-height: vw(200);
    display: flex;
    flex-direction: row;
    align-items: center;

    >*{
        flex-grow: 1;
    }
} 


</style>




