<template>
<div>
  <div class="recorder" :class="{record: current == STATES.RECORD, stop: current == STATES.STOP}" @click="click"></div>
  <button @click="testAudio">测试试听</button>
  </div>
</template>
<script>
import { Toast } from 'mint-ui';
export default {
    name: 'RecorderButton',
    created(){
        this.current = this.STATES.STOP;
    },
    data(){
        return {
            STATES:{
                RECORD:1,
                STOP:2
            },
            EVENTS:{
                //状态改变事件
                CHANGE: "changeEvent"
            },
            current: null,
            filePath: null,
        }  
    },
    methods:{
        click(){
            if(this.current == this.STATES.STOP){
                this.filePath = this.record();
            }else{
                this.stop();
            }
            this.$emit(this.EVENTS.CHANGE, this.current);
        },
        record(){
            this.$bridge.call("record", {}, (res) => {
                this.current = this.STATES.RECORD;
                Toast('start record');
            });
        },
        stop(){
            this.$bridge.call("stopRecord", {}, (res) => {
                this.current = this.STATES.STOP
                this.filePath = sprintf('file://%s', res);
            })
        },
        testAudio(){
            Toast(this.filePath);
            try{
                    this.$audio.play({
                        src: [this.filePath]
                    });
                }
                catch(e){
                    Toast(e);
            }
        }
    }
}

</script>

<style scoped lang="scss">
    .record:after{
        content:'停止录音';
    }

    .stop:after{
        content:'开始录音';
    }
</style>





