<template>
    <div class="catelog-panel">
    <div class="title">
        顺序播放(共五节)
        <span class="close" @click="close">X</span>
    </div>
    <ul
        v-infinite-scroll="loadMore"
        infinite-scroll-disabled="loading"
        infinite-scroll-distance="10">
        <li v-for="(item, key) in catelog" @click="selectItem(item)">
            <span>{{item.title}}</span>   
            <span>{{item.img}}</span>
            <span>{{item.owner}}</span>
            <span>{{item.time}}</span>
        </li>
    </ul>
</div>
</template>

<script>
export default {
  name: 'CatelogPanel',
  data(){
      return {
          EVENTS:{
              CLOSE: "close",
              SELECT_ITEM: "selectItem"
          }
      }
  },
  props: ['catelog'],
  methods: {
      close(){
        this.$emit(this.EVENTS.CLOSE);
      },
      loadMore(){

      },
      selectItem(item){
          this.$emit(this.EVENTS.SELECT_ITEM, item);
      }
  }
}
</script>

<style scoped lang="scss">
@import "../../assets/css/global.scss";
    .catelog-panel{
        background: #fff;
        position: fixed;
        bottom:0;
        border:1px solid #ccc;
        width:100%;
        .title{
            border-bottom: 1px solid #ccc;
            position: relative;
            .close{
                position: absolute;
                right:0
            }
        }
        li{
            padding: vw(10);
            border-bottom: 1px solid #ccc;
        }
        li:last-child{
            border-bottom:none
        }
    }
</style>





