import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Selection from '../components/reading/Selection'
import Reading from '../components/reading/Reading'
import Activity from '../components/Activity'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Reading',
      component: Reading,
    },
    {
      path: '/Activity',
      name: 'Activity',
      component: Activity
    },
    {
      path: '/Selection',
      name: 'Selection',
      component: Selection,
    },
    {
      path: '/Reading/:id',
      name: 'Reading',
      component: Reading,
    }
  ]
})



