<template>
  <div class="player" @click="click" 
  :class="{play: current == STATES.PLAY, stop: current == STATES.STOP, pause: current == STATES.PAUSE, loading: current == STATES.LOADING, loaded: current == STATES.LOADED}"
  ></div>
</template>

<script>
export default {
  name: "PlayerButton",
  data(){
      return {
        STATES:{
          PLAY: 1,
          STOP: 2,
          PAUSE: 3,
          LOADING:4, 
          LOADED:5,
        },
        current: null,
        path: "static/test.mp3",
        duration: 0,
        EVENTS:{
            STATE_CHANGE:"stateChange",
            DURATION_LOAD: "durationLoad",
            TIME_CHANGE: "timeChange"
        }
      }
  },
  created(){
      this.current = this.STATES.STOP;
  },
  methods:{
      click(){
        if(this.current == this.STATES.STOP){
            this.current = this.STATES.LOADING;
            this.$audio.play({
                src: [this.path], 
                onplay: () => {
                    this.current = this.STATES.PLAY;
                },
                onload: () => {
                    this.duration = this.$audio.duration();
                    this.$emit(this.EVENTS.DURATION_LOAD, this.duration.toFixed());
                    this.current = this.STATES.LOADED;
                },
                onend: () => {
                    this.current = this.STATES.STOP;
                },
                onpause: () => {
                    this.current = this.STATES.PAUSE;
                },
                onplaying: (time) => {
                    this.$emit(this.EVENTS.TIME_CHANGE, time);
                }
            });
        }else if(this.current == this.STATES.PLAY){
            this.$audio.pause();
        }else if(this.current == this.STATES.PAUSE){
            this.$audio.resume();
        }
      },
      stop(){
          if(this.current == this.STATES.PLAY)this.$audio.pause();
      }
    }
}
</script>


<style lang="scss" scoped>
.play:after{
    content:'暂停'
}

.stop:after{
    content: '播放'
}

.pause:after{
    content: '继续播放'
}

.loading:after{
    content: '加载中';
}

.loaded:after{
    content:'加载完成';
}
</style>




